  },
  methods : {
    initAction : function() {
      console.log(localStorage.getItem("login"));
      if (localStorage.getItem("login") != "") {
        //ログイン処理
        console.log("ログイン開始");
        localStorage.setItem("login", "");
        const provider = new TwitterAuthProvider()
        const auth = getAuth();
        setPersistence(auth, browserLocalPersistence)
        .then(() => {
          signInWithRedirect(auth, provider);
        });
      } else {
        //リダイレクト後処理
        getRedirectResult(getAuth()).then((result) => {
          //連携成功時
          console.log(result);
          let uid = result.user.providerData[0].uid;
          let iconUrl = result._tokenResponse.photoUrl;
          let displayName = result._tokenResponse.displayName;
          let displayId = result._tokenResponse.screenName;

          let params = new URLSearchParams();
          params.append('twitterUID', uid);
          params.append('twitterIconUrl', iconUrl);
          params.append('displayName', displayName);
          params.append('twitterId', displayId);
          let loginParam = localStorage.getItem("login_param");
          console.log(loginParam)
          params.append('kbn', loginParam);

          console.log(uid + " " + iconUrl  + " " + displayName + " " + displayId);
          axios.post(this.$root.SERVER_PATH + "login", params)
          .then((response) => {
            let resData = response.data;
            console.log(resData);
            if (resData["status"] == this.$root.API_FAILED_LOGIN) {
              //IDとPASSでログイン失敗
              // this.errorMessageData.login = "ログインに失敗しました";
              localStorage.setItem("top_param", "FAILED");
              this.$router.push("/");
              return;
            } else if (resData["status"] == this.$root.API_FAILED_NODATA) {
              //データなし（アカウント未作成）
              // this.errorMessageData.login = "アカウントが作成されていません\n新規登録を行ってください";
              localStorage.setItem("top_param", "NODATA");
              this.$router.push("/");
              return;
            }
            localStorage.setItem("twitterId", displayId);
            localStorage.setItem("displayName", displayName);
            localStorage.setItem("top_param", "");
            this.$router.push("/"); 
          });
        });

        getAuth().onAuthStateChanged((user) => {
          if (!user) {
            console.log("not login");
            //ログイン失敗時
            // let loginParam = localStorage.getItem("login_param");
            localStorage.setItem("login_param", "");
            this.$router.push("/");
          }
        });
      }
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      console.log("ooooooooooooooooooooo1");
      vm.initAction();
